@import url('https://fonts.googleapis.com/css2?family=Quicksand:wght@500&display=swap');
@import url("https://cdnjs.cloudflare.com/ajax/libs/semantic-ui/2.4.1/semantic.min.css");

*{
  font-family: "Quicksand";
  color: inherit;
  
}
.popup{
  position:fixed;
  top:0;
  left:0;
  width:100%;
  height: 100vh;
  background-color: rgba(0,0,0,0.7);
  display: flex;
  justify-content: center;
  align-items: center;

}

.popup-inner{
  position:relative;
  width: 100%;
  max-width: 640px;
  border-radius: 5px;
  background-color: #f2f2f2;
  padding: 20px;
}

.popup-inner .close-btn{
  position: absolute;
  top: 16px;
  right: 16px;
}
.App{
  background-image: url('./images/icebackground.jpg');
  background-size: 100%;
}
.wrapper{
  display: flex;
}

/* Nav Section */
.navbar{
  height: 50px;
  display: flex;
  align-items: center;
  margin: 0 auto;
  border-bottom: 1px solid #f2f2f2;
  background-color: #00FFFF;
}

.navbar .title{
  font-size: 25px;
  color: black;
  font-weight: bold;
  margin-left: 20px;
  margin-right: 10px;
  white-space: nowrap;
}

.navbar h1{
  padding-left: 20px;
  color: 'black';
}

.navbar .logo{
  max-width: 3%;
  height: auto;
  padding-left: 10px;
}

.navbar .links{
  margin-left: 1100px;
  color: black;
}
.navbar .icon{
  max-width: 4%;
  margin-right: 3px;
}

.navbar .icon2{
  max-width: 3%;
  margin-right: 3px;
}

.Button{
  margin-left: auto;
  color: red;
  background-color: #00FFFF;
  
}

.navbar a{
  margin-left: 16px;
  text-decoration: none;
  padding: 6px;
  color: black;
  transition-duration: 0.4s;
  border-radius: 10px;
}

.navbar .links a:hover{
  text-decoration: underline;
  box-shadow: 0 12px 16px 0 rgba(0,0,0,0.24), 0 17px 50px 0 rgba(0,0,0,0.19);
  background-color: white;
  box-sizing: 100px;

  
}

/* Nav Section Ends*/
/* Home Section */

.home .intro{
  display: flex;
  align-items: center;
  text-decoration: underline;
  color: white;
  
}

.home .pin{
  max-width: 4%;
  height: auto;
  padding-right: 20px;
}

.home .nhlLogo{
  max-width: 7%;
  height: auto;
  margin: 0 auto;
  margin-right: 0px;

}
.home .triviaText{
  font-size: 30px;
  background-color:#00FFFF;
  margin-left: 30px;
  border: black;
  border-style: ridge;
  color: black;
  border-radius: 10px;
  transition-duration: 0.4s;
  padding-top: 30px;
  justify-content: center;
  padding-right: 15px;
 
}
.home .icon{
  max-width: 50px;
}

.triviaText:hover{
  background-color:rgba(231, 202, 36, 0.952);
  box-shadow: black;
  
}
/* Home Section Ends */
/* News Section */

.news{
  margin-left: 20px;
  margin-top: 20px;
}

.news .newsTitle{
  background-color: #00FFFF;
  padding: 20px;
  text-align: center;
  border-radius: 10px;
  border: black;
  border-style:groove;
  border-width: 5px;
  border-radius: 20px;
}

/* News Section Ends */
/* Twitter Section */
.twitterFeed{
  margin: 0 auto;
  margin-right: 20px;
  margin-top: 20px;
}

.twitterFeed .twitterTitle{
  background-color: #00FFFF;
  padding: 20px;
  text-align: center;
  border: black;
  border-style:groove;
  border-width: 5px;
  border-radius: 20px;
}

.twitterFeed .twitterImg{
  border: black;
  border-style:groove;
  border-width: 5px;
  border-radius: 20px;
}
/* Twitter Section Ends*/

/* Teams Section */
.teams .title{
  padding-top: 40px;
  display: flex;
  align-items: center;
  text-decoration: underline;
}
.teams .titleText{
  font-size: 60px;
  margin-left: auto;
}
.teams .easternConference{
  height: auto;
  width: 24%;
  margin: 0 auto;
  margin-right: 120px;
  margin-bottom: 0px;
}

.teams .westernConference{
  height: auto;
  width: 25%;
  margin-left: 110px; 
}

.teams .teamTables{
  display: flex;
  height: 553px;

}
.teams th{
  
  border: 3px solid;
  font-weight: bolder;
}
.teams table,td{
  border: 1px solid;
  border-collapse: collapse;
  justify-content: left;
  text-align: left;
  text-justify:auto;
  width: 500px;
  color: black;


}
.teams td:hover{
  text-decoration: underline;
  background-color: lightgray
}
.teams .eastTeamTable{
  margin: 0 auto;
  margin-top: 0px;
  margin-right: 70px;
  background-color: white;
  border: black;
  border-style: groove;
  
}
.teams .westTeamTable{
  margin: 0 auto;
  margin-left: 70px;
  background-color: white;
  border: black;
  border-style: groove; 
  padding-top: 500px;

}

.teams .teamLogo{
  height: auto;
  width: 40px;
}

/* Teams Section Ends */

/* Team Section */
.team .title{
  display: flex;
}
.team .titleText{
  font-size: 30px;
  text-align: center;
  background-color:#00FFFF;
  border-radius: 15px;
  border-style: groove;
  border-color: black;
  border-width: 6px;
  padding: 10px;
  margin-left: auto;
  height: 85px;
 

  
}

.team .teamLogo{
  height: auto;
  width: 200px;
  margin-left: 10px;
}

.team .nhlLogo{
  height: auto;
